  z-index: 100;
}
@media (max-width: 767px) {
  .filter-panel {
    position: relative !important;
    top: 0 !important;
    margin-bottom: 1rem;
  }

  .job-card {
    margin-bottom: 1.5rem;
  }
}

@media screen and (max-width: 768px) {
  .signup-container {
     flex-direction: column-reverse;
    padding: 20px;
  }

  .signup-illustration {
    order: 0; /* image first */
    width: 100%;
    margin-bottom: 20px;
  }

  .signup-form {
    order: 1; /* form second */
    width: 50%;
  }
}

@media (max-width: 992px) {
  .navbar-collapse {
    display: flex !important;
    justify-content: center !important;
  }

  .nav-links {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .nav-actions {
    display: flex;
    justify-content: center;
    gap: 12px;
  }

  .nav-btn {
    margin: 0; /* Remove margins that may interfere */
  }
}

@media (max-width: 767px) {
  .row.job-list {
    display: flex;
    flex-direction: column;
  }

  .row.job-list > [class*="col-"] {
    width: 100% !important;
    max-width: 100% !important;
    flex: 0 0 100% !important;
  }

  .job-card {
    width: 100%;
    margin-bottom: 1rem;
  }
}

/* Common action button style */
.action-btn {
  font-weight: 500;
  border-radius: 6px;
  padding: 4px 14px;
  border: none;
  color: #fff;
  margin-right: 8px; /* spacing between buttons */
  transition: all 0.3s ease;
  cursor: pointer;
}

.action-btn:last-child {
  margin-right: 0;
}

/* Approve button colors */
.approve-btn {
  background-color: #5e2975; /* Bootstrap success green */
}

.approve-btn:hover {
  background-color: #a266c5; /* darker on hover */
  /* box-shadow: 0 2px 6px rgba(40, 167, 69, 0.5); */
}

/* Reject button colors */
.reject-btn {
  background-color: #dc3545; /* Bootstrap danger red */
}

.reject-btn:hover {
  background-color: #c82333; /* darker on hover */
  box-shadow: 0 2px 6px rgba(220, 53, 69, 0.5);
}

/* Disabled state styling */
.action-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}


/* Standardized form card width & look */
.standard-form-card {
  background-color:  #ffffff;
  max-width: 800px;           /*  consistent width across screens */
  margin: 0 auto;
  transition: box-shadow 0.3s ease;
}

.standard-form-card:hover {
  box-shadow: 0 6px 20px rgba(0,0,0,0.12);
}

/* Inputs/selects consistent spacing & rounding */
.standard-form-card .form-control,
.standard-form-card .form-select,
.standard-form-card textarea {
  padding: 10px 14px;
  border-radius: 6px;
}

/* Labels look crisp */
.standard-form-card .form-label {
  font-weight: 600;
  margin-bottom: 6px;
}

/* Primary CTA look (shared) */
.main-btn {
  padding: 8px 20px;
  font-weight: 600;
  border-radius: 8px;
}
